import { _decorator, Component, Node, Vec3, Collider2D, Contact2DType, instantiate, IPhysics2DContact, Prefab } from 'cc';
import { Plane } from './Plane';
const { ccclass, property } = _decorator;

@ccclass('BulletE')
export class BulletE extends Component {
    private bulletSpeed: number = 10;
    private posX: number;
    private posY: number;
    @property(Prefab)
    public destroyPre: Prefab = null;
    start() {
        let collider = this.getComponent(Collider2D);
        if(collider){
            collider.on(Contact2DType.BEGIN_CONTACT, this.onBeginContact, this);
            collider.on(Contact2DType.END_CONTACT, this.onEndContact, this);
        }
        
    }

    update(deltaTime: number) {
        this.bulletMove();
    }
    bulletMove(){
            this.posX = this.node.position.x;
            this.posY = this.node.position.y;

            this.posY -= this.bulletSpeed;
            let posBullet = new Vec3(this.posX, this.posY);
            this.node.setPosition(posBullet);
    }
    destroyB(){
        let destroyPrefab = this.destroyPre;
        let prefabInstance = instantiate(destroyPrefab);
        prefabInstance.parent = this.node.parent;
        prefabInstance.setWorldPosition(this.node.worldPosition);
    }
    onBeginContact(selfCollider: Collider2D, otherCollider: Collider2D, contact: IPhysics2DContact | null){
        if(otherCollider.node.name.startsWith('Plane')){
            this.destroyB();
            this.scheduleOnce(() => {selfCollider.node?.destroy()});
        }
    }
    onEndContact(selfCollider: Collider2D, otherCollider: Collider2D, contact: IPhysics2DContact | null){

    }
}


